<template>
    <nav class="navbar navbar-expand-lg navbar-light" id="mainNav">
        <div class="container p-4 px-lg-5">
            <button type="button" class="btn fs-1" @click="toggleSideNav" v-if="isMobile">
                <i class="bi bi-arrow-bar-right" v-if="!isSideNav"></i>
                <i class="bi bi-arrow-bar-left" v-if="isSideNav"></i>
            </button>
            <a class="navbar-brand mx-0" href="index.html"><img src="img/logo_black.png" alt="Grimlight" style="width: 100%;"></a>
            <button class="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarResponsive">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" :class="{ hide: isCollapse}" id="navbarResponsive">
                <ul class="navbar-nav ms-auto py-4 py-lg-0" @click="isCollapse = !isCollapse">
                    <router-link to="/"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Heros</a></li></router-link>
                    <router-link to="/haven"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Haven</a></li></router-link>
                    <router-link to="/equipment"><li class="nav-item"><a class="nav-link px-lg-3 py-3 py-lg-4">Weapon</a></li></router-link>
                </ul>
            </div>
        </div>
    </nav>
</template>
  
<script>
/* eslint-disable */
import { ref } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useSideNav } from "@/store/SideNav"
import { storeToRefs } from 'pinia'
export default {
    name: 'TopNav',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { isSideNav } = storeToRefs(useSideNav())
        const isCollapse = ref(false)

        const toggleSideNav = () => {
            useSideNav().toggleSideNav()
        }

        return {
            isMobile,
            isSideNav,
            isCollapse,
            toggleSideNav,
        }
    }
}
</script>

<style scoped>
.navbar-brand {
    width: 15%;
}
.nav-link {
    color: black;
}

@media screen and (max-width: 768px) {
    .navbar-brand {
        width: 25%;
    }
}
</style>